<template>
<section id="main-content" class="main-content">
    <loader :is-visible="isLoading"></loader>
    <section class="page-action">
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{$t('Import Product Type')}}</span>
                </div>
            </div>
        </div>
    </section>
    <div class="user-guide-custom d-none">
        {{$t('User_Guide_ProductTypeSampleImport')}}
    </div>
    <div class="border p-3">
        <Form ref="ItemTypeForm">
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('Sample File')}}:</label>
                        <div class="input-group mb-3">
                            <div class="form-control">
                                <i class="fa fa-file-excel-o pr-1 text-success"></i>{{$t('ProductTypeSample')}}.xls
                            </div>
                            <div class="input-group-append downloadProductSample">
                                <span class="input-group-text" id="basic-addon2">
                                    <a href="https://cogniterblob.blob.core.windows.net/global/SampleFiles/sample-file-asset-producttype-import.xls" class="" download>
                                        <i class="fa fa-download text-primary"></i>
                                    </a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-lg-4">
                    <div class="form-group">
                        <label>{{$t('Upload a File')}}:<span class="text-danger">*</span></label>
                        <Field v-slot='{errors}' :rules="{ 'required': true }">
                            <div class="input-group">
                                <div class="custom-file ProductTypeSampleFile align-items-start">
                                    <input type="file" name='FileSrc' id='flAvatar' tabindex="-1" v-on:change="setFile($event)" style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                    <div class="bootstrap-filestyle input-group">
                                        <input type="text" :class="{'form-control disabled':true,'is-invalid':errors[0]}" disabled="" v-model="attachmentName" />
                                        <div class="input-group-append" style="cursor:pointer;">
                                            <span class="group-span-filestyle input-group-btn input-group-text bg-white" tabindex="0">
                                                <label title="Click here to select a file." for='flAvatar' class="mb-0 font-weight-normal ">{{$t('FileControlText')}}</label>
                                            </span>
                                            <span class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                <a href="javascript:;" class="round-icon-small btn-danger-light" title="Clear" @click="Clear"><i class="fa fa-undo"></i></a>
                                            </span>
                                        </div>
                                        <span v-if="errors[0]" name="FileSrc" class="invalid-feedback">Upload a File Field is Required.</span>
                                    </div>
                                </div>
                            </div>
                        </Field>
                    </div>
                </div>
            </div>
            <div class="row flex-row-reverse sm_mt-3">
                <div class="col-lg-6 text-right">
                    <a href="javascript:void(0);" @click="UploadProductSampleFile()" :title="$t('ImportFromExcelFile')" class="btn btn-info formbtn mr-1"><i class="fa fa-download pr-2"></i>{{$t('ImportFromExcelFile')}}</a>
                    <a title="" class="btn btn-danger formbtn my-2" @click="onCancel()" data-toggle-tooltip="tooltip" data-original-title="Cancel"><i class="fa fa-close mr-2"></i>Cancel</a>
                </div>
            </div>
        </Form>
    </div>
    <ProductTypeSample ref="myChild" v-if="ShowImportProductType" :filepath="filepath"></ProductTypeSample>
</section>
</template>

<script>
import ProductTypeSample from '../../components/Modal/ProductTypeSample.vue'
import DataService from '../../service/DataService';

export default {
    components: {
        ProductTypeSample
    },
    data: function () {
        return {
            isLoading: false,
            file: null,
            filepath: '',
            excelFileName: null,
            reqfrom: null,
            expListing: null,
            trips: [],
            categories: [],
            projects: [],
            currencies: [],
            tasks: [],
            expLimitJsonBack: [],
            check_EXPENSE_LIMIT: false,
            userDateFormat: null,
            files: new FormData(),
            value: null,
            attachmentName: '',
            ShowImportProductType: false,
        }
    },
    methods: {
        onCancel: function () {
            this.$router.push({
                name: 'ProductType'
            });
        },
        setFile: function (e) {
            this.Clear();         
            this.attachmentName = e.target.files[0].name;
            this.file = e.target.files[0];
            this.excelFileName = e.target.files[0].name;
        },
        Clear: function () {          
            this.attachmentName = '';
        },
        UploadProductSampleFile() {
            debugger
            var vm = this;
            vm.$refs.ItemTypeForm.validate().then(result => {
                debugger
                if (result) {
                    if ($("#flAvatar").val() != "") {
                        var file = $("#flAvatar").val().split('\\').pop();
                        var allowFileTypesExt = '.xls,.xlsx';
                        var extension = file.replace(/^.*\./, '');
                        if (!allowFileTypesExt.includes(extension)) {
                            vm.ShowAlert(vm.$t('InvalidSampleFileUploadMsg', [file, allowFileTypesExt]), "warning", true, vm.$t('Alert'));
                        } else {
                            vm.confirmR(vm.$t("bulkImportConfirmationMsg"), true, (vm.$t("Delete") + "  " + vm.$t("REPORT_ID")), function () {
                                const formdata = new FormData();
                                formdata.append("file", vm.file);
                                vm.isLoading = true;
                                DataService.ImportProductSamplePost(formdata).then(function (response) {
                                    vm.isLoading = false;
                                    var data = response.data;
                                    if (data.Status == 0) {
                                        vm.filepath = data.Msg;
                                        vm.ShowImportProductType = vm.filepath != '' ? true : false;
                                    } else {
                                        vm.ShowAlert(data.Msg, "warning", true, vm.$t("Alert"));
                                    }
                                });
                                vm.isLoading = false;
                            });
                        }
                    } else {
                        vm.ShowAlert(vm.$t("PleaseUploadExcelFile"), "warning", true, vm.$t("Alert"));
                    }
                }
            });
            vm.$emit('ImportProduct')

        },
    }
}
</script>
